function updateBreadcrump() {
  const mainBreadcrump = document.getElementById("bread-crump");
  const currPage = window.location.pathname;
  const pagesMap = {
    "/": "Главная",
    "/materials": "Материалы",
    "/performances": "Спектакли",
  };
  mainBreadcrump.textContent = pagesMap[currPage];
}