function SkedApp(baseUrl = "") {
  const _this = this;
  this.BASE_URL = baseUrl;

  this.state = [];
  this.needToSave = false;
  this.todayISODate = new Date(Date.now());
  const wdays = ["Вс", "Пн", "Вт", "Ср", "Чт", "Пт", "Сб"];
  this.dayFrom = document.getElementById("day-from");
  this.monthFrom = document.getElementById("month-from");
  this.yearFrom = document.getElementById("year-from");
  this.dayTo = document.getElementById("day-to");
  this.monthTo = document.getElementById("month-to");
  this.yearTo = document.getElementById("year-to");
  const skedMainTb = document.getElementById("sked-main-tb");
  const nextSkedBtn = document.getElementById("next-sked-bt");
  const saveSkedBtn = document.getElementById("save-sked-bt");
  const prevSkedBtn = document.getElementById("prev-sked-bt");
  const newSkedBtn = document.getElementById("new-sked-bt");
  
  this.fillWithWorkers = function(workers) {

    const currWorkers = workers ? workers : _this.workers;
    let tr, td, sel, opt1, opt2, opt3, dayNum = 0;

    for(let i=0; i<currWorkers.length; i++) {
      tr = document.createElement("tr");
      tr.setAttribute("class", "table-row-worker");
      td = document.createElement("td");
      td.textContent = currWorkers[i].username;
      tr.appendChild(td);
      
      while(dayNum < 14) {
        td = document.createElement("td");
        sel = document.createElement("select");
        sel.setAttribute("class", "shift-choose");

        opt1 = document.createElement("option");
        opt2 = document.createElement("option");
        opt3 = document.createElement("option");
        
        opt1.textContent = "–";
        opt2.textContent = "8.30";
        opt3.textContent = "16";
        sel.appendChild(opt1);
        sel.appendChild(opt2);
        sel.appendChild(opt3);

        if(!workers) {
          opt1.setAttribute("selected", "true");
        } else {
          for(key in workers[i].shifts) {
            if(opt1.textContent == workers[i].shifts[key]) {
              opt1.setAttribute("selected", "true");
            }
            else if(opt2.textContent == workers[i].shifts[key]) {
              opt2.setAttribute("selected", "true");
            } 
            else if(opt3.textContent == workers[i].shifts[key]) {
              opt3.setAttribute("selected", "true");
            }
          }
        }

        td.appendChild(sel);
        tr.appendChild(td);
        dayNum++;
      }
      skedMainTb.appendChild(tr);
      dayNum = 0;
    }
  };

  this.saveState = function() {
    const skedState = [];
    const tableRows = document.getElementsByClassName("table-row-worker");

    for(let i=0; i<tableRows.length; i++) {
      let shifts = {};

      for(let b=1; b<tableRows[i].childNodes.length; b++) {
        shifts[b] = tableRows[i].childNodes[b].firstChild.value;
      }

      let worker = {};
      worker.username = tableRows[i].firstChild.textContent;
      worker.shifts = shifts;

      skedState.push(worker);
    }

    // to form saving post query
    const fromDate = `${_this.fromISODate.getMonth()+1}/${_this.fromISODate.getDate()}/${_this.fromISODate.getFullYear()}`;
    const toDate = `${_this.toISODate.getMonth()+1}/${_this.toISODate.getDate()}/${_this.toISODate.getFullYear()}`;
    
    // send data to the server to save
    request("POST", _this.BASE_URL + "/save-sked", `state=${JSON.stringify(skedState)}&from=${fromDate}&to=${toDate}`, function(status, res) {
      if (status == "success") {
        console.log("Sked draft was saved");
      } else if (status == "error") {
        console.error("Error during saving current sked draft state");
      }
    });
  };

  this.fillUpperDates = function(from, to) {
    let dtFrom = new Date(from);
    let dtTo = new Date(to);

    _this.dayFrom.textContent = dtFrom.getDate();
    _this.monthFrom.textContent = dtFrom.getMonth()+1;
    _this.yearFrom.textContent = dtFrom.getFullYear();
    _this.dayTo.textContent = dtTo.getDate();
    _this.monthTo.textContent = dtTo.getMonth()+1;
    _this.yearTo.textContent = dtTo.getFullYear();
  };

  this.getSked = function(from, to) {
    const postQuery = from ? `from=${from}` : `to=${to}`;
  
    request("POST", _this.BASE_URL + "/sked", postQuery, function (status, res) {
      if (status == "success") {
        const responseData = JSON.parse(res.responseText);

        // if no previous sked or it is empty dates, hide previous button
        if(responseData.prev == false || !responseData.prev) {
          prevSkedBtn.style.display = "none";
        } else {
          prevSkedBtn.style.display = "block";
        }
        // if no next sked, or it is empty dates, hide next button
        if(responseData.next == false| !responseData.next) {
          nextSkedBtn.style.display = "none";
          newSkedBtn.style.display = "block";
        } else {
          nextSkedBtn.style.display = "block";
          newSkedBtn.style.display = "none";
        }

        // if it is sked data, fill with it
        if(responseData.sked) {
          const skedData = responseData.sked.data;

          _this.fillUpperDates(responseData.sked.from, responseData.sked.to);
          _this.fillTableHeader(responseData.sked.from, responseData.dates);
          _this.fillWithWorkers(skedData);
          // set current date from
          _this.fromISODate = new Date(responseData.sked.from);
          _this.toISODate = new Date(responseData.sked.to);
        } 
        // otherwise, just fill new sked with current dates
        else {
          // hide new btn
          newSkedBtn.style.display = "none";
          prevSkedBtn.style.display = "block";

          _this.fillTableHeader(responseData.from, responseData.dates);
          _this.fillUpperDates(responseData.from, _this.toISODate);
          _this.fillWithWorkers();
          // set current date from
          _this.fromISODate = new Date(responseData.from);
        }
      }
      else if (status == "error") {
        return;
      }
    });
  };

  this.init = function() {
    if(nextSkedBtn) {
      nextSkedBtn.addEventListener("click", function() {
        _this.saveState();
        _this.needToSave = false;
        _this.nextSked();
      });      
    } 
    if(saveSkedBtn) {
      saveSkedBtn.onclick = function() {
        _this.saveState();
      };
    }
    if(newSkedBtn) {
      newSkedBtn.onclick = function() {
        _this.needToSave = true;
        let fromPrev = confirm("Создать график с последнего числа предыдущего графика? При отмене график создастся с сегодняшнего дня.");
        let query;
        if(fromPrev) {
          query = `${_this.toISODate.getMonth()+1}/${_this.toISODate.getDate()}/${_this.toISODate.getFullYear()}`;
        } else {
          const today = new Date(Date.now());
          query = `${today.getMonth()+1}/${today.getDate()}/${today.getFullYear()}`;
        }

        const rows = document.getElementsByClassName("table-row-worker");
        // clear table before;
        while(rows[0]) {
          rows[0].remove();
        }

        _this.getSked(query);
      };
    }
    if(prevSkedBtn) {
      prevSkedBtn.onclick = function() {
        _this.saveState();
        _this.needToSave = false;
        let dt = `${_this.fromISODate.getMonth()+1}/${_this.fromISODate.getDate()}/${_this.fromISODate.getFullYear()}`;
        const rows = document.getElementsByClassName("table-row-worker");

        // clear table before;
        while(rows[0]) {
          rows[0].remove();
        }
        _this.getSked(null, dt);
      };
    }
    // get last sked created
    _this.getSked("LAST");
  };

  this.nextSked = function() {
    const rows = document.getElementsByClassName("table-row-worker");
    while(rows[0]) {
      rows[0].remove();
    }
    const newFrom = `${_this.toISODate.getMonth()+1}/${_this.toISODate.getDate()}/${_this.toISODate.getFullYear()}`;
    _this.getSked(newFrom);
  };

  this.fillTableHeader = function(dateFrom, dates) {
    let dateeFrom = new Date(dateFrom).getDate();
 
    let weekday = new Date(dateFrom).getDay();
    const weekdates = document.getElementsByClassName("weekdate");
    let next = false;
 
    for(let i=0; i<weekdates.length; i++) {
      for(let b=0; b<dates.length; b++) {
        if(dates[b].date == dateeFrom) {
          // if dates type is next month
          if(next) {
            if(dates[b].type != "next") {
              continue;
            } else {
              weekdates[i].textContent = dates[b].date + " "+ wdays[weekday];
            }
          } else {
            weekdates[i].textContent = dates[b].date + " "+ wdays[weekday];
          }
          
          // set date To by last date
          if(i+1 == weekdates.length && _this.needToSave == true) {
            _this.toISODate = new Date(dates[b].iso);
          }
       
          if(dates[b+1].type == "next"&& !next) {
            next=true;
            dateeFrom = 1;
          } else {
            dateeFrom++;
          }
          // update weekday
          if(weekday <6) {
            weekday++
          } else {
            weekday = 0;
          }
          break;
        }
      }
    }
  };

  // setInterval(function() {
  //   if(_this.needToSave == true) {
  //     _this.saveState();
  //   }
  // }, 1000*60);
};